footer{
   background: var(--color-primary);
   padding: 3rem 0;
   text-align: center;
   font-size: 1rem;
   margin-top: 1rem;
}

footer a{
   color: var(--color-bg);
}

.footer__logo{
   font-size: 2rem;
   font-weight: 800;
   margin-bottom: 2rem;
   display: inline-block;
}

.links{
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   gap: 2rem;
   margin: 2rem auto 3rem;
}

.footer__copyright{
   margin-bottom: 4rem;
   color: var(--color-bg);
}

@media screen and (max-width: 600px){
   .links{
      flex-direction: column;
      gap: 1.5rem;
   } 
}