header {
   height: 100vh;
   padding-top: 7rem;
   overflow: hidden;
}

.header_comp{
   text-align: center;
   height: 100%;
   position: relative;
}

.cta {
   margin-top: 2.5rem;
   display: flex;
   gap: 1.2rem;
   justify-content: center;
}

.socials {
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 0.8rem;
   position: absolute;
   left: 0;
   bottom: 3rem;
}

.me {
   background: linear-gradient(var(--color-primary), transparent);
   width: 22rem;
   height: 30rem;
   position: absolute;
   left: calc(50% - 11rem);
   margin-top: 4rem;
   border-radius: 12rem 12rem 0 0;
   overflow: hidden;
   padding: 5rem 1.5rem 1.5rem 0; /*trbl*/
}

.scroll_down{
   position: absolute;
   right: -2.3rem;
   bottom: 5rem;
   transform: rotate(90deg);
   font-weight: 300;
   font-size: 0.9rem;
}

@media screen and (max-width: 1024px) {
   .header_comp {
      height: 68vh;
   }
}

@media screen and (max-width: 600px) {
   .header_comp{
      height: 100vh;
   }
   .socials,
   .scroll_down{
      display: none;
   }
}

